import SellerProduct from "@/Components/Seller/SellerProduct";
import { GetToken } from "@/utilities/cookies";
import {
  getcategory,
  getsellerdata,
  getsellercount,
} from "@/api/serversideaction/category";

import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "Seller List";
  return createMetadata({
    title: title,
  });
}


export default async function page() {
  const [token, category, sellerdata, sellercount] = await Promise.all([
    GetToken(),
    getcategory(),
    getsellerdata(0),
    getsellercount(),
  ]);

  return (
    <SellerProduct
      token={token}
      category={category?.data}
      sellerdata={sellerdata}
      sellercount={sellercount}
    />
  );
}
